import React from 'react';
import Result from 'ant-design-pro/lib/Result';
import { Button, Icon, Col, Row, Card, Spin } from 'antd';
import { injectIntl } from 'react-intl';
import { withRouter } from 'react-router-dom';
import styled from 'styled-components';

import { userGetProcessByUuid } from './../../services';
import functions from './../../utils/functions';

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

const ResultWrapper = styled(Result)`
  &.antd-pro-result-result .antd-pro-result-icon > .antd-pro-result-error {
    color: #f58722;
  }
`;

class PayCancel extends React.Component {
  state = { process: null, loading: true };

  componentDidMount = () => {
    this.fetchProcess();
  };

  fetchProcess = async () => {
    const {
      dashboard,
      history,
      match: { params },
    } = this.props;

    if (params && params.uuid) {
      const response = await userGetProcessByUuid(params.uuid);

      if (response.data) {
        this.setState({
          process: response.data,
        });
      } else {
        if (dashboard) {
          history.push('/dashboard/process-services');
        }
      }
    }
    this.setState({
      loading: false,
    });
  };

  goBack = () => {
    const { dashboard, history } = this.props;
    if (dashboard) {
      history.push('/dashboard/process-services');
    }
  };

  render() {
    const {
      intl: { messages },
    } = this.props;

    const actions = (
      <Button onClick={this.goBack} type="primary">
        {messages.default.goBack.toUpperCase()}
      </Button>
    );

    const extra = (
      <div>
        <div
          style={{
            fontSize: 16,
            color: 'rgba(0, 0, 0, 0.85)',
            fontWeight: 500,
            marginBottom: 16,
          }}
        >
          {messages.payment.cancel.detailsOfTheProcess.toUpperCase()}：
        </div>
        <div style={{ marginBottom: 16 }}>
          {messages.payment.cancel.filename.toUpperCase()}
          <span style={{ marginLeft: 16 }}>
            {this.state.process ? this.state.process.fileName : null}
          </span>
        </div>
        <div style={{ marginBottom: 16 }}>
          {messages.payment.cancel.nameOfProcess.toUpperCase()}
          <span style={{ marginLeft: 16 }}>
            {this.state.process ? jsUcfirst(this.state.process.processName) : null}
          </span>
        </div>
        <div style={{ marginBottom: 16 }}>
          {messages.payment.cancel.language.toUpperCase()}
          <span style={{ marginLeft: 16 }}>
            {this.state.process ? (
              <React.Fragment>
                {this.state.process.engineSource.toUpperCase()}
                <Icon type="arrow-right" />
                {this.state.process.engineTarget.toUpperCase()}
              </React.Fragment>
            ) : null}
          </span>
        </div>
        <div style={{ marginBottom: 16 }}>
          {messages.payment.cancel.status.toUpperCase()}
          <span style={{ marginLeft: 16 }}>
            {this.state.process ? functions.getStatusByText(this.state.process.status) : null}
          </span>
        </div>
        {this.state.process && this.state.process.quoteSelected && (
          <div style={{ marginBottom: 16 }}>
            {messages.payment.cancel.quotedPrice.toUpperCase()}
            <span style={{ marginLeft: 16 }}>
              {this.state.process ? (
                <div>
                  <Icon type="euro" /> &nbsp;
                  {this.state.process.quoteSelected.price}
                </div>
              ) : null}
            </span>
          </div>
        )}
      </div>
    );

    if (!this.state.loading) {
      return (
        <Spin spinning={this.state.loading} tip="Loading...">
          <Row justify="center" type="flex">
            <Col xs={24} md={14}>
              <Card style={{ margin: 24 }}>
                <ResultWrapper
                  type="error"
                  title={messages.payment.cancel.canceled}
                  description={messages.payment.cancel.paymentProcessCanceled.toUpperCase()}
                  extra={extra}
                  actions={actions}
                />
              </Card>
            </Col>
          </Row>
        </Spin>
      );
    }
    return <div />;
  }
}

export default withRouter(injectIntl(PayCancel));
